.description {
  margin-top: 45px;
  margin-bottom: 18px;

  @include vp-1440 {
    margin-bottom: 80px;
  }

  .container {
    display: grid;

    @include vp-1440 {
      grid-template-columns: 1fr 1fr;
    }
  }

  .description__img {
    width: 100%;
    height: 216px;

    @include vp-768 {
      height: 410px;
    }

    @include vp-1440 {
      height: 399px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .description__text {
    @include vp-1440 {
      padding: 30px 50px;
      margin-left: 70px;
    }
  }
}
