.service-hero {
  position: relative;
  margin-bottom: 80px;

  @include vp-768 {
    margin-bottom: 62px;
  }

  @include vp-1440 {
    margin-bottom: 116px;
  }

  .title {
    // @include vp-768 {
      padding: 0;
    // }
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    background: linear-gradient(110.78deg, rgba(30, 43, 59, 0.5) 15.86%, rgba(30, 43, 59, 0.00312519) 73.29%, rgba(30, 43, 59, 0) 73.29%, rgba(30, 43, 59, 0) 73.29%);
    z-index: 0;
  }

  img {
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-position: right bottom;
  }

  .container {
    position: relative;
    width: 100%;
    max-width: none;
    height: 575px;

    @include vp-768 {
      height: 576px;
    }

    @include vp-1440 {
      height: 624px;
    }
  }

  .service-hero__text-wrapper {
    position: absolute;
    left: 16px;
    right: 16px;
    top: 2%;
    bottom: 50%;
    display: block;
    height: max-content;
    max-width: 1240px;
    z-index: 1;

    @include vp-768 {
      transform: translate(-50%, 0);
      left: 50%;
      padding: 16px;
      width: 100%;
      top: 3%;
    }

    @include vp-1440 {
      top: 50%;
      transform: translate(-50%, -50%);
      padding: 0;
    }

    p {
      @include vp-768 {
        margin-top: 50px;
        max-width: 504px;
      }
    }
  }

  h2 {
    border: none;
    text-align: left;
    color: $color-default-white;

    span {
      color: $color-twine;
    }

    br {
      display: none;

      @include vp-768 {
        display: block;
      }
    }
  }

  p {
    color: $color-default-white;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
  }
}

.service-hero--supply {
  margin-bottom: 10px;
  img {
    object-position: center center;
  }
}
